*, ::after, ::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html {
    font-size: 10px; }

body {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    color: #777777;
    font-size: 1.6rem;
    letter-spacing: 0.2rem;
    padding: 5rem;
    background-color: white; }



.container {
    background-color: #F7F7F7; }

.header {
    position: relative;
    height: 95vh;
    background: linear-gradient(90deg, rgba(186, 133, 84, 0.7) 0.03%, rgba(1, 1, 1, 0.7) 99.94%), url(../img/header-bg.jpg) center  / cover no-repeat;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
    &__logo {
        position: absolute;
        width: 5.6rem;
        height: 7rem;
        left: 5rem;
        top: 5rem; }
    &__box {
        position: absolute;
        width: 100%;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center; } }

.title-first {
    color: #FFFFFF;
    &__main {
        display: block;
        font-weight: 800;
        font-size: 5rem;
        line-height: 6rem;
        letter-spacing: 1rem;
        text-transform: uppercase;
        margin-bottom: 1rem;
        animation-name: kekw;
        animation-duration: 5s;
        animation-delay: 2s;
        animation-timing-function: easy;
        backface-visibility: hidden;
        animation-fill-mode: backwards; }
    &__secondary {
        display: block;
        font-weight: 100;
        font-size: 4rem;
        line-height: 4.8rem;
        letter-spacing: 0.5rem;
        animation-name: kekw2;
        animation-duration: 5s;
        animation-delay: 2s;
        animation-timing-function: easy;
        backface-visibility: hidden;
        animation-fill-mode: backwards;
        margin-bottom: 6.5rem; } }

.btn {
    position: relative;
    &:link, &:visited {
        text-transform: uppercase;
        text-decoration: none;
        color: #777777;
        padding: 1.5rem 4rem;
        background-color: white;
        border-radius: 5rem;
        display: inline-block;
        transition: all 0.2s;
        font-size: 2rem; }

    &:hover {
        transform: translateY(-0.4rem);
        box-shadow: (0px 4px 4px rgba(0, 0, 0, 0.5));
        &::after {
            opacity: 0;
            transform: scaleX(1.5) scaleY(1.7); } }
    &:active {
        transform: translateY(-2px);
        box-shadow: (0px 4px 4px rgba(0, 0, 0, 0.3)); }
    &::after {
        content: "";
        background: white;
        width: 100%;
        height: 100%;
        border-radius: 5rem;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -10;
        transition: all .2s; }
    &_animated {
        animation: kekwBTN 5s ease-out 2s;
        animation-fill-mode: backwards; } }


@keyframes kekw {
    0% {
        opacity: 0;
        transform: translate(-10rem); }
    50% {
        transform: translate(2rem); }

    100% {
        opacity: 1;
        transform: translate(0rem); } }

@keyframes kekw2 {
    0% {
        opacity: 0;
        transform: translate(10rem); }
    50% {
        transform: translate(-2rem); }

    100% {
        opacity: 1;
        transform: translate(0rem); } }

@keyframes kekwBTN {
    0% {
        opacity: 0;
        transform: translateY(10rem); }

    100% {
        opacity: 1;
        transform: translateY(0rem); } }
